.intro { background-image: url("../img/home-bg.png"); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding-top: 170px; padding-bottom: 140px; position: relative; }

@media only screen and (max-width: 767px) { .intro { padding: 70px 0; background-position: 45% 0; } }

.intro__title h1 { font-size: 80px; font-weight: bold; color: #131c2e; margin-bottom: 45px; margin-top: 50px; }

@media only screen and (max-width: 1200px) { .intro__title h1 { font-size: 60px; } }

@media only screen and (max-width: 767px) { .intro__title h1 { font-size: 34px; margin-bottom: 25px; } }

.intro__title h1 span { background: #ff0057; width: 18px; height: 18px; border-radius: 100%; display: inline-block; margin-left: 5px; }

@media only screen and (max-width: 767px) { .intro__title h1 span { width: 10px; height: 10px; } }

.intro__title p { font-size: 20px; font-weight: normal; color: #131c2e; line-height: 1.5; max-width: 480px; }

@media only screen and (max-width: 767px) { .intro__title p { font-size: 16px; } }

.intro__app { margin-top: 120px; display: flex; align-items: center; flex-wrap: wrap; }

@media only screen and (max-width: 767px) { .intro__app { margin-top: 50px; } }

.intro__app__download { font-size: 18px; font-weight: bold; color: #131c2e; margin-right: 45px; margin-bottom: 15px; }

@media only screen and (max-width: 767px) { .intro__app__download { font-size: 16px; margin-right: 12px; padding-top: 5px; } }

.intro__app__download i { font-size: 12px; margin-left: 13px; }

@media only screen and (max-width: 767px) { .intro__app__download i { margin-left: 10px; } }

.intro__app__link { font-size: 14px; font-weight: bold; color: #ff0057; display: flex; align-items: center; margin-right: 30px; margin-bottom: 15px; }

@media only screen and (max-width: 767px) { .intro__app__link { margin-right: 12px; font-size: 13px; }
  .intro__app__link:last-child { margin-right: 0; } }

.intro__app__link i { font-size: 38px; margin-right: 10px; }

@media only screen and (max-width: 767px) { .intro__app__link i { font-size: 35px; } }

.intro__img { position: relative; display: flex; align-items: flex-start; height: 100%; justify-content: flex-end; }

@media only screen and (max-width: 767px) { .intro__img { flex-direction: column; align-items: center; } }

.intro__img img { max-width: 550px; width: calc(100% + 50px); position: absolute; left: -130px; bottom: -20px; }

@media only screen and (max-width: 1040px) { .intro__img img { position: relative; left: 0; bottom: 0; margin-top: 50px; width: 100%; } }

@media only screen and (max-width: 767px) { .intro__img img { margin-top: 40px; } }

@media only screen and (max-width: 767px) { .intro__list { margin-top: 40px; } }

